import React, { useState, useEffect } from 'react';
import { 
  Phone, Plus, Trash2, Edit2, RefreshCw, Settings2, X
} from 'lucide-react';

const mockVoices = {
  english: [
    { id: "en-US-1", name: "Emma (Female)" },
    { id: "en-US-2", name: "James (Male)" },
    { id: "en-US-3", name: "Sarah (Female)" }
  ],
  tamil: [
    { id: "ta-IN-1", name: "Priya (Female)" },
    { id: "ta-IN-2", name: "Kumar (Male)" },
    { id: "ta-IN-3", name: "Deepa (Female)" }
  ],
  hindi: [
    { id: "hi-IN-1", name: "Aarav (Male)" },
    { id: "hi-IN-2", name: "Diya (Female)" },
    { id: "hi-IN-3", name: "Arjun (Male)" }
  ]
};

const defaultConfig = {
  voices: {
    english: { 
      id: "en-US-1",
      name: "Emma (Female)",
      settings: { stability: 0.5, similarityBoost: 0.75 }
    },
    tamil: { 
      id: "ta-IN-1",
      name: "Priya (Female)", 
      settings: { stability: 0.5, similarityBoost: 0.75 }
    },
    hindi: { 
      id: "hi-IN-1",
      name: "Aarav (Male)",
      settings: { stability: 0.5, similarityBoost: 0.75 }
    }
  },
  messages: {
    english: "Welcome to Kalyan Jewellers. Today’s gold rate is ₹5850 per gram",
    tamil: "கல்யாண் ஜுவல்லெர்ஸுக்கு வரவேற்கிறோம். இன்று தங்க விலை கிராமுக்கு ₹5850.",
    hindi: "कल्याण ज्वेलर्स में आपका स्वागत है। आज सोने का भाव ₹5850 प्रति ग्राम है।"
  }
};

const OutreachManager = ({
  contacts,
  setContacts,
  onStartOutreach,
  onBackToDashboard
}) => {
  const [selectedContacts, setSelectedContacts] = useState([]);
  const [isOutreachActive, setIsOutreachActive] = useState(false);
  const [progress, setProgress] = useState(0);
  const [showSettings, setShowSettings] = useState(false);
  const [voiceConfig, setVoiceConfig] = useState(defaultConfig);

  const handleAdd = () => {
    setContacts([...contacts, { name: '', phone: '', language: 'english', followups: 0 }]);
  };

  const toggleContactSelection = (phone) => {
    setSelectedContacts((prev) =>
      prev.includes(phone) ? prev.filter((p) => p !== phone) : [...prev, phone]
    );
  };

  const selectAllContacts = () => {
    setSelectedContacts(contacts.map((c) => c.phone));
  };

  const deselectAllContacts = () => {
    setSelectedContacts([]);
  };

  const removeContact = (phone) => {
    setContacts(contacts.filter((c) => c.phone !== phone));
  };

  const editContact = (updatedContact) => {
    setContacts(
      contacts.map((c) => (c.phone === updatedContact.phone ? updatedContact : c))
    );
  };

  const startOutreach = () => {
    setIsOutreachActive(true);
    onStartOutreach(selectedContacts, voiceConfig);
    
    // Auto-end after 7 seconds
    const interval = setInterval(() => {
      setProgress(prev => {
        const newProgress = prev + (100/7); // 7 seconds total
        return newProgress > 100 ? 100 : newProgress;
      });
    }, 1000);

    setTimeout(() => {
      setIsOutreachActive(false);
      setProgress(0);
      clearInterval(interval);
      setSelectedContacts([]);
    }, 7000);
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold">Outreach Manager</h2>
        <div className="flex items-center gap-4">
          {!isOutreachActive && (
            <button
              onClick={() => setShowSettings(true)}
              className="btn btn-outline btn-sm flex items-center gap-2"
            >
              <Settings2 className="w-4 h-4" />
              Voice Settings
            </button>
          )}
          {isOutreachActive && (
            <div className="flex items-center gap-2">
              <div className="h-2 w-32 bg-gray-200 rounded">
                <div 
                  className="h-full bg-blue-500 rounded transition-all duration-500"
                  style={{ width: `${progress}%` }}
                />
              </div>
              <span className="text-sm text-gray-600">
                {Math.round(progress)}%
              </span>
            </div>
          )}
        </div>
      </div>

      {/* Settings Modal */}
      {showSettings && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-xl p-6 max-w-2xl w-full max-h-[90vh] overflow-y-auto">
            <div className="flex justify-between items-center mb-6">
              <h3 className="text-lg font-semibold">Voice Settings</h3>
              <button
                onClick={() => setShowSettings(false)}
                className="btn btn-ghost btn-sm"
              >
                <X className="w-4 h-4" />
              </button>
            </div>

            <div className="space-y-6">
              {Object.entries(voiceConfig.voices).map(([lang, voice]) => (
                <div key={lang} className="border-b pb-6 last:border-0">
                  <h4 className="text-lg font-medium text-gray-700 capitalize mb-4">{lang} Settings</h4>
                  
                  <div className="space-y-4">
                    {/* Voice Selection */}
                    <div>
                      <label className="block mb-1 text-sm font-medium text-gray-700">Voice Selection</label>
                      <select
                        value={voice.id}
                        onChange={(e) => {
                          const selectedVoice = mockVoices[lang].find(v => v.id === e.target.value);
                          setVoiceConfig({
                            ...voiceConfig,
                            voices: {
                              ...voiceConfig.voices,
                              [lang]: {
                                ...voice,
                                id: selectedVoice.id,
                                name: selectedVoice.name
                              }
                            }
                          });
                        }}
                        className="select w-full"
                      >
                        {mockVoices[lang].map(v => (
                          <option key={v.id} value={v.id}>{v.name}</option>
                        ))}
                      </select>
                    </div>

                    {/* Message Content */}
                    <div>
                      <label className="block mb-1 text-sm font-medium text-gray-700">Message Content</label>
                      <textarea
                        rows={3}
                        value={voiceConfig.messages?.[lang] || defaultConfig.messages[lang]}
                        onChange={(e) =>
                          setVoiceConfig({
                            ...voiceConfig,
                            messages: {
                              ...voiceConfig.messages,
                              [lang]: e.target.value
                            }
                          })
                        }
                        className="textarea w-full"
                        placeholder={`Enter ${lang} message...`}
                      />
                    </div>

                    {/* Voice Settings */}
                    <div className="grid grid-cols-2 gap-4 pt-2">
                      <div>
                        <label className="block mb-1 text-sm text-gray-500">Stability</label>
                        <input
                          type="number"
                          min={0}
                          max={1}
                          step={0.05}
                          value={voice.settings.stability}
                          onChange={(e) =>
                            setVoiceConfig({
                              ...voiceConfig,
                              voices: {
                                ...voiceConfig.voices,
                                [lang]: {
                                  ...voice,
                                  settings: {
                                    ...voice.settings,
                                    stability: Math.min(1, Math.max(0, Number(e.target.value))),
                                  },
                                },
                              },
                            })
                          }
                          className="input w-full"
                        />
                        <p className="mt-1 text-xs text-gray-500">
                          Higher values maintain voice stability
                        </p>
                      </div>
                      <div>
                        <label className="block mb-1 text-sm text-gray-500">Similarity Boost</label>
                        <input
                          type="number"
                          min={0}
                          max={1}
                          step={0.05}
                          value={voice.settings.similarityBoost}
                          onChange={(e) =>
                            setVoiceConfig({
                              ...voiceConfig,
                              voices: {
                                ...voiceConfig.voices,
                                [lang]: {
                                  ...voice,
                                  settings: {
                                    ...voice.settings,
                                    similarityBoost: Math.min(1, Math.max(0, Number(e.target.value))),
                                  },
                                },
                              },
                            })
                          }
                          className="input w-full"
                        />
                        <p className="mt-1 text-xs text-gray-500">
                          Higher values increase voice similarity
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              ))}

              <div className="flex justify-end mt-6">
                <button
                  onClick={() => setShowSettings(false)}
                  className="btn btn-primary btn-sm"
                >
                  Save Settings
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      <div className="bg-white rounded shadow p-6">
        <div className="flex justify-between items-center mb-4">
          <div className="flex items-center gap-4">
            <h3 className="text-lg font-semibold">Contact List</h3>
            <button onClick={handleAdd} className="btn btn-primary btn-sm flex items-center gap-2">
              <Plus className="w-4 h-4" /> Add Contact
            </button>
          </div>
          {selectedContacts.length > 0 && !isOutreachActive && (
            <button
              onClick={startOutreach}
              className="btn btn-success btn-sm flex items-center gap-2"
            >
              <Phone className="w-4 h-4" />
              Start Outreach ({selectedContacts.length})
            </button>
          )}
          {isOutreachActive && (
            <div className="flex items-center gap-2 text-blue-600">
              <RefreshCw className="w-4 h-4 animate-spin" />
              <span>Outreach in progress...</span>
            </div>
          )}
        </div>

        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead>
              <tr>
                <th className="px-4 py-2">
                  <input
                    type="checkbox"
                    checked={contacts.length > 0 && selectedContacts.length === contacts.length}
                    onChange={(e) =>
                      e.target.checked ? selectAllContacts() : deselectAllContacts()
                    }
                    className="rounded border-gray-300 text-blue-600"
                  />
                </th>
                <th className="px-4 py-2 text-left">Name</th>
                <th className="px-4 py-2 text-left">Phone</th>
                <th className="px-4 py-2 text-left">Language</th>
                <th className="px-4 py-2 text-left">Followups</th>
                <th className="px-4 py-2">Actions</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200">
              {contacts.map((contact) => (
                <tr
                  key={contact.phone}
                  className={selectedContacts.includes(contact.phone) ? 'bg-blue-50' : ''}
                >
                  <td className="px-4 py-2">
                    <input
                      type="checkbox"
                      checked={selectedContacts.includes(contact.phone)}
                      onChange={() => toggleContactSelection(contact.phone)}
                      className="rounded border-gray-300 text-blue-600"
                      disabled={isOutreachActive}
                    />
                  </td>
                  <td className="px-4 py-2">{contact.name}</td>
                  <td className="px-4 py-2">{contact.phone}</td>
                  <td className="px-4 py-2 capitalize">{contact.language}</td>
                  <td className="px-4 py-2">{contact.followups}</td>
                  <td className="px-4 py-2">
                    <div className="flex gap-2">
                      <button
                        onClick={() => editContact(contact)}
                        className="btn btn-ghost btn-sm"
                        aria-label={`Edit ${contact.name}`}
                        disabled={isOutreachActive}
                      >
                        <Edit2 className="w-4 h-4" />
                      </button>
                      <button
                        onClick={() => removeContact(contact.phone)}
                        className="btn btn-ghost btn-sm text-red-500"
                        aria-label={`Remove ${contact.name}`}
                        disabled={isOutreachActive}
                      >
                        <Trash2 className="w-4 h-4" />
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      <div className="flex justify-end">
        <button
          onClick={onBackToDashboard}
          className="btn btn-outline btn-sm"
          disabled={isOutreachActive}
        >
          Back to Dashboard
        </button>
      </div>
    </div>
  );
};

export default OutreachManager;
