import React, { useEffect, useState } from "react";
import { PlayCircle, Calendar } from "lucide-react";
import { useDispatch, useSelector } from "react-redux";
import { fetchCallAnalytics, fetchPersonAnalytics } from "../store/slices/callSlice";

import TrendChart from "../components/analytics/TrendChart";
import CallStatistics from "../components/analytics/CallStatistics";
import SentimentAnalysis from "../components/analytics/SentimentAnalysis";
import CallSummary from "../components/analytics/CallSummary";
import RecordingPlayer from "../components/analytics/RecordingPlayer";
import CallsTable from "../components/analytics/CallsTable";
import CallPersonDashboard from "./CallPersonDashboard";

const mockCalls = [
  {
    id: 1,
    name: "Ravi Kumar",
    phone: "+91 9876543210",
    language: "tamil",
    status: "completed",
    duration: "2m 45s",
    timestamp: "2 mins ago",
    sentiment: "positive"
  },
  {
    id: 2,
    name: "Priya Singh",
    phone: "+91 9876543211",
    language: "english",
    status: "completed",
    duration: "3m 15s",
    timestamp: "15 mins ago",
    sentiment: "neutral"
  },
  {
    id: 3,
    name: "Amit Patel",
    phone: "+91 9876543212",
    language: "hindi",
    status: "completed",
    duration: "1m 30s",
    timestamp: "25 mins ago",
    sentiment: "positive"
  },
  {
    id: 4,
    name: "Sara Khan",
    phone: "+91 9876543213",
    language: "english",
    status: "completed",
    duration: "4m 15s",
    timestamp: "1 hour ago",
    sentiment: "positive"
  },
  {
    id: 5,
    name: "Vijay Kumar",
    phone: "+91 9876543214",
    language: "tamil",
    status: "completed",
    duration: "2m 30s",
    timestamp: "2 hours ago",
    sentiment: "neutral"
  }
];

const mockTrends = {
  dates: ["Jul 25", "Jul 26", "Jul 27", "Jul 28", "Jul 29", "Jul 30"],
  calls: [20, 25, 30, 28, 22, 25],
  durations: [140, 150, 145, 160, 155, 150]
};

const mockSentiments = {
  positive: 65,
  neutral: 25,
  negative: 10,
  keywords: ["pricing", "features", "quality", "support"],
  details: [
    { date: "Jul 30", positive: 70, neutral: 20, negative: 10 },
    { date: "Jul 29", positive: 65, neutral: 25, negative: 10 },
    { date: "Jul 28", positive: 60, neutral: 30, negative: 10 }
  ],
  overall: 0.75,
  segments: [
    { score: 0.8, text: "Customer expressed interest in gold rate", timestamp: "0:30" },
    { score: 0.6, text: "Discussed current market trends", timestamp: "1:45" },
    { score: 0.9, text: "Positive response to special offer", timestamp: "2:15" }
  ]
};

const CallDashboard = ({ onStartCampaign }) => {
  const dispatch = useDispatch();

  const {
    statistics,
    trends,
    sentiments,
    calls
  } = useSelector((state) => state.calls);

  const [dateRange] = useState('Jul 30, 2025');
  const [selectedPersonId, setSelectedPersonId] = useState(null);

  useEffect(() => {
    dispatch(fetchCallAnalytics());
  }, [dispatch]);

  useEffect(() => {
    if (selectedPersonId) {
      dispatch(fetchPersonAnalytics(selectedPersonId));
    }
  }, [selectedPersonId, dispatch]);

  const handlePersonSelect = (personId) => {
    setSelectedPersonId(personId);
  };

  const handleBackToDashboard = () => {
    setSelectedPersonId(null);
  };

  return (
    <div className="space-y-8">
      {selectedPersonId ? (
        <CallPersonDashboard
          personId={selectedPersonId}
          onBack={handleBackToDashboard}
        />
      ) : (
        <>
          {/* Header Section */}
          <div>
            <div className="flex justify-between items-start">
              <div>
                <h1 className="text-2xl font-semibold mb-2">Kalyan Voice Campaign Dashboard</h1>
                <p className="text-gray-600">
                  Welcome to your multilingual voice campaign management system. Monitor real-time performance 
                  and launch targeted campaigns.
                </p>
              </div>
              <button 
                onClick={onStartCampaign}
                className="px-6 py-2.5 text-sm font-medium text-white bg-blue-600 rounded-lg hover:bg-blue-700 flex items-center gap-2"
              >
                <PlayCircle className="h-5 w-5 text-white" />
                <span className="text-white">Start New Campaign</span>
              </button>
            </div>
          </div>

          {/* Date Filter */}
          <div className="flex justify-between items-center mb-6">
            <button className="flex items-center space-x-2 px-4 py-2 text-sm font-medium text-gray-600 bg-white rounded-lg border hover:bg-gray-50">
              <Calendar className="h-4 w-4" />
              <span>{dateRange}</span>
            </button>
          </div>

          {/* Summary Stats */}
          <div className="grid grid-cols-4 gap-6 mb-6">
            <div className="p-4 bg-blue-50 rounded-lg">
              <h3 className="text-xs font-medium text-gray-500 mb-1">Total calls</h3>
              <div className="flex items-baseline">
                <span className="text-2xl font-semibold mr-2">150</span>
                <span className="text-sm text-gray-500">Compared to yesterday</span>
              </div>
            </div>
            <div className="p-4 bg-blue-50 rounded-lg">
              <h3 className="text-xs font-medium text-gray-500 mb-1">Total inbound calls</h3>
              <div className="flex items-baseline">
                <span className="text-2xl font-semibold mr-2">31</span>
                <span className="text-sm text-red-500">-10%</span>
              </div>
            </div>
            <div className="p-4 bg-blue-50 rounded-lg">
              <h3 className="text-xs font-medium text-gray-500 mb-1">Total outbound calls</h3>
              <div className="flex items-baseline">
                <span className="text-2xl font-semibold mr-2">22</span>
                <span className="text-sm text-red-500">-15%</span>
              </div>
            </div>
            <div className="p-4 bg-blue-50 rounded-lg">
              <h3 className="text-xs font-medium text-gray-500 mb-1">Total Ext 2 Ext calls</h3>
              <div className="flex items-baseline">
                <span className="text-2xl font-semibold mr-2">38</span>
                <span className="text-sm text-green-500">+15%</span>
              </div>
            </div>
          </div>

          {/* Secondary Stats */}
          <div className="grid grid-cols-4 gap-6 mb-6">
            <div className="p-4 bg-gray-50 rounded-lg">
              <div className="flex justify-between">
                <div>
                  <h3 className="text-xs font-medium text-gray-500 mb-1">Total missed calls</h3>
                  <p className="text-2xl font-semibold">10</p>
                </div>
                <span className="text-sm text-red-500">-10%</span>
              </div>
            </div>
            <div className="p-4 bg-gray-50 rounded-lg">
              <div className="flex justify-between">
                <div>
                  <h3 className="text-xs font-medium text-gray-500 mb-1">Total abandoned calls</h3>
                  <p className="text-2xl font-semibold">74</p>
                </div>
                <span className="text-sm text-red-500">-10%</span>
              </div>
            </div>
            <div className="p-4 bg-gray-50 rounded-lg">
              <div className="flex justify-between">
                <div>
                  <h3 className="text-xs font-medium text-gray-500 mb-1">Unique callers</h3>
                  <p className="text-2xl font-semibold">45</p>
                </div>
                <span className="text-sm text-red-500">-10%</span>
              </div>
            </div>
            <div className="p-4 bg-gray-50 rounded-lg">
              <div className="flex justify-between">
                <div>
                  <h3 className="text-xs font-medium text-gray-500 mb-1">Total voice mails</h3>
                  <p className="text-2xl font-semibold">1</p>
                </div>
                <span className="text-sm text-red-500">-10%</span>
              </div>
            </div>
          </div>

          {/* Trend Chart */}
          <div className="bg-white p-6 rounded-lg shadow mb-6">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-sm font-medium text-gray-900">Call Summary Chart</h2>
            </div>
            <TrendChart data={mockTrends} />
          </div>

          {/* Call Statistics */}
          <CallStatistics stats={statistics} />

          {/* Recording Player */}
          <div className="bg-white p-6 rounded-lg shadow mb-6">
            <RecordingPlayer 
              recording={calls && calls.length > 0 ? {
                url: calls[0].recordingUrl,
                duration: calls[0].duration,
                caller: calls[0].caller,
                transcription: calls[0].transcription,
                keyMoments: calls[0].keyMoments || []
              } : null} 
            />
          </div>

          {/* Sentiment Analysis and Call Summary */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
            <div className="bg-white p-6 rounded-lg shadow">
              <SentimentAnalysis sentiment={mockSentiments} />
            </div>
            <div className="bg-white p-6 rounded-lg shadow">
              <CallSummary 
                call={{
                  id: "CALL-123",
                  timestamp: "2025-07-30 14:30",
                  duration: "3:45",
                  type: "Outbound",
                  language: "English",
                  keyPoints: [
                    "Customer interested in current gold rates",
                    "Discussed Kalyan Jewellers offerings",
                    "Requested follow-up about special promotions"
                  ],
                  actionItems: [
                    "Send current gold rate chart",
                    "Schedule follow-up call about promotions",
                    "Share catalog of latest collections"
                  ],
                  tags: ["High Interest", "Follow-up Required", "Gold Rate Inquiry"]
                }}
              />
            </div>
          </div>

          {/* Recent Calls Table */}
          <div className="bg-white rounded-lg shadow-sm">
            <div className="p-6">
              <h2 className="text-lg font-semibold mb-4">Recent Calls</h2>
              <CallsTable calls={mockCalls} onSelectPerson={handlePersonSelect} />
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default CallDashboard;
