import React, { useState } from "react";
import { Provider } from "react-redux";
import store from "./store";
import CallDashboard from "./pages/CallDashboard";
import VoiceCampaign from "./pages/VoiceCampaign";
import { Phone, Settings, PhoneCall, Users, BarChart2 } from 'lucide-react';
import "./App.css";

function App() {
  const [currentView, setCurrentView] = useState('analytics'); // 'analytics' or 'campaign'
  const [showSettings, setShowSettings] = useState(false);
  const [campaignConfig, setCampaignConfig] = useState({
    batchSize: 2,
    callDelay: 5,
    voices: {
      english: {
        id: "21m00Tcm4TlvDq8ikWAM",
        settings: { stability: 0.5, similarityBoost: 0.75 }
      },
      tamil: {
        id: "EXAVITQu4vr4xnSDxMaL",
        settings: { stability: 0.5, similarityBoost: 0.75 }
      },
      hindi: {
        id: "EXAVITQu4vr4xnSDxMaL",
        settings: { stability: 0.5, similarityBoost: 0.75 }
      }
    },
    messages: {
      english: "Welcome to Kalyan Jewellers. Today's gold rate is ₹5850 per gram.",
      tamil: "கல்யாண் ஜுவல்லெர்ஸுக்கு வரவேற்கிறோம். இன்று தங்க விலை கிராமுக்கு ₹5850.",
      hindi: "कल्याण ज्वेलर्स में आपका स्वागत है। आज सोने का भाव ₹5850 प्रति ग्राम है।"
    }
  });

  const [contacts, setContacts] = useState([
    { name: "Ravi", phone: "+14155550101", language: "tamil", followups: 0 },
    { name: "Seema", phone: "+14155550102", language: "english", followups: 1 },
    { name: "Amit", phone: "+14155550103", language: "hindi", followups: 2 }
  ]);

  const [campaignStatus, setCampaignStatus] = useState({
    isRunning: false,
    currentBatch: [],
    processedCount: 0,
    status: 'idle'
  });

  const handleStartCampaign = () => {
    setCampaignStatus({
      isRunning: true,
      currentBatch: contacts.slice(0, campaignConfig.batchSize),
      processedCount: 0,
      status: 'processing'
    });
  };

  return (
    <Provider store={store}>
      <div className="min-h-screen bg-gray-50">
        {/* Navbar */}
        <nav className="bg-white border-b">
          <div className="max-w-7xl mx-auto px-4">
            <div className="flex justify-between h-16">
              <div className="flex items-center">
                <img src="https://i.postimg.cc/Fzxhw8rq/image.png" alt="Fusefy" className="h-8 w-auto" />
                <h1 className="text-xl font-semibold ml-3">Fusefy</h1>
              </div>
              <div className="flex items-center space-x-4">
                <button
                  onClick={() => setCurrentView('analytics')}
                  className={`px-4 py-2 text-sm font-medium rounded ${
                    currentView === 'analytics'
                      ? 'text-blue-600'
                      : 'text-gray-600 hover:text-blue-600'
                  }`}
                >
                  Dashboard
                </button>
                <button
                  onClick={() => setCurrentView('campaign')}
                  className={`px-4 py-2 text-sm font-medium rounded ${
                    currentView === 'campaign'
                      ? 'text-blue-600'
                      : 'text-gray-600 hover:text-blue-600'
                  }`}
                >
                  Campaign
                </button>
              </div>
            </div>
          </div>
        </nav>

        {/* Main Content */}
        <main className="max-w-7xl mx-auto py-6 px-4">
          {currentView === 'analytics' ? (
            <CallDashboard onStartCampaign={() => setCurrentView('campaign')} />
          ) : (
            <VoiceCampaign
              config={campaignConfig}
              onUpdateConfig={setCampaignConfig}
              showSettings={showSettings}
              onCloseSettings={() => setShowSettings(false)}
              contacts={contacts}
              setContacts={setContacts}
              campaignStatus={campaignStatus}
              onStartCampaign={handleStartCampaign}
              onBackToDashboard={() => setCurrentView('analytics')}
            />
          )}
        </main>
      </div>
    </Provider>
  );
}

export default App;
