import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';

// Register ChartJS components
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

export default function TrendChart({ data }) {
  const chartData = {
    labels: ["Jul 21", "Jul 22", "Jul 23", "Jul 24", "Jul 25", "Jul 26", "Jul 27", "Jul 28", "Jul 29", "Jul 30", "Aug 01", "Aug 02", "Aug 03", "Aug 04"],
    datasets: [
      {
        label: "Inbound",
        data: [2, 3, 2, 4, 3, 5, 4, 3, 4, 5, 3, 4, 3, 4],
        borderColor: "#4287f5",
        backgroundColor: "transparent",
        tension: 0.1,
        pointRadius: 4,
        borderWidth: 1.5,
        fill: false,
      },
      {
        label: "Outbound",
        data: [1, 2, 3, 2, 4, 3, 2, 3, 2, 3, 2, 3, 2, 3],
        borderColor: "#f54242",
        backgroundColor: "transparent",
        tension: 0.1,
        pointRadius: 4,
        borderWidth: 1.5,
        fill: false,
      },
      {
        label: "Ext 2 Ext",
        data: [3, 2, 4, 3, 2, 4, 3, 2, 3, 4, 2, 3, 4, 3],
        borderColor: "#42f575",
        backgroundColor: "transparent",
        tension: 0.1,
        pointRadius: 4,
        borderWidth: 1.5,
        fill: false,
      }
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: "bottom",
        align: "start",
        labels: {
          boxWidth: 8,
          usePointStyle: true,
          pointStyle: "circle",
          padding: 25,
          color: "#6b7280",
          font: {
            size: 11,
            family: "'Inter', sans-serif"
          }
        },
      },
      tooltip: {
        enabled: true,
        intersect: false,
        mode: 'index',
      }
    },
    scales: {
      y: {
        display: true,
        beginAtZero: true,
        max: 10,
        grid: {
          color: "#f3f4f6",
          drawBorder: false,
          drawTicks: false,
        },
        ticks: {
          stepSize: 2,
          padding: 15,
          color: "#9ca3af",
          font: {
            size: 11,
            family: "'Inter', sans-serif"
          }
        },
        border: {
          display: false
        }
      },
      x: {
        grid: {
          display: false,
          drawBorder: false,
          drawTicks: false,
        },
        ticks: {
          maxRotation: 0,
          padding: 8,
          color: "#9ca3af",
          font: {
            size: 11,
            family: "'Inter', sans-serif"
          }
        },
        border: {
          display: false
        }
      },
    },
    layout: {
      padding: {
        left: 0,
        right: 0,
        top: 0,
        bottom: 0
      }
    }
  };

  return (
    <div className="chart-container h-[300px]">
      <Line data={chartData} options={options} />
    </div>
  );
}
