import React from 'react';

export default function CallStatistics({ stats }) {
  if (!stats) return null;

  return (
    <div className="grid grid-cols-3 gap-6">
      {/* Call Statistics */}
      <div className="bg-white p-6 rounded-lg shadow">
        <h3 className="text-sm font-medium text-gray-500 mb-4">Call Statistics</h3>
        <div className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div>
              <p className="text-xs text-gray-500">Total call time</p>
              <p className="text-sm font-semibold">{stats.callTime?.total || '0'}</p>
            </div>
            <div>
              <p className="text-xs text-gray-500">Average call time</p>
              <p className="text-sm font-semibold">{stats.callTime?.average || '0'}</p>
            </div>
            <div>
              <p className="text-xs text-gray-500">Total talk time</p>
              <p className="text-sm font-semibold">{stats.callTime?.talkTime || '0'}</p>
            </div>
          </div>
        </div>
      </div>

      {/* Inbound Stats */}
      <div className="bg-white p-6 rounded-lg shadow">
        <h3 className="text-sm font-medium text-gray-500 mb-4">Inbound Statistics</h3>
        <div className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div>
              <p className="text-xs text-gray-500">Total inbound call</p>
              <p className="text-sm font-semibold">{stats.inbound?.total || '0'}</p>
            </div>
            <div>
              <p className="text-xs text-gray-500">Total inbound active calls</p>
              <p className="text-sm font-semibold">{stats.inbound?.active || '0'}</p>
            </div>
            <div>
              <p className="text-xs text-gray-500">Total inbound answered</p>
              <p className="text-sm font-semibold">{stats.inbound?.answered || '0'}</p>
            </div>
          </div>
        </div>
      </div>

      {/* Outbound Stats */}
      <div className="bg-white p-6 rounded-lg shadow">
        <h3 className="text-sm font-medium text-gray-500 mb-4">Outbound Statistics</h3>
        <div className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div>
              <p className="text-xs text-gray-500">Total outbound calls</p>
              <p className="text-sm font-semibold">{stats.outbound?.total || '0'}</p>
            </div>
            <div>
              <p className="text-xs text-gray-500">Total outbound active calls</p>
              <p className="text-sm font-semibold">{stats.outbound?.active || '0'}</p>
            </div>
            <div>
              <p className="text-xs text-gray-500">Total outbound answered</p>
              <p className="text-sm font-semibold">{stats.outbound?.answered || '0'}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
