import { useState } from "react";
import { 
  Mic, 
  PlayCircle, 
  PauseCircle, 
  Download, 
  MessageSquare, 
  Clock,
  Star
} from "lucide-react";

export default function RecordingPlayer({ recording }) {
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [showTranscript, setShowTranscript] = useState(true);

  if (!recording) return null;

  const togglePlay = () => {
    setIsPlaying(!isPlaying);
    // TODO: Implement actual audio control
  };

  const formatTime = (time) => {
    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time % 60);
    return `${minutes}:${seconds.toString().padStart(2, '0')}`;
  };

  return (
    <div className="bg-white rounded-lg h-full flex flex-col">
      {/* Header */}
      <div className="p-4 border-b border-gray-100">
        <div className="flex items-center justify-between mb-2">
          <div className="flex items-center gap-2">
            <Mic className="w-5 h-5 text-blue-600" />
            <h3 className="font-medium text-gray-900">Latest Call Recording</h3>
          </div>
          <span className="text-sm text-gray-500">{recording.duration}</span>
        </div>
        <div className="flex items-center justify-between">
          <span className="text-sm text-gray-600">{recording.caller}</span>
          <a 
            href={recording.url} 
            download 
            className="flex items-center gap-1 text-sm text-blue-600 hover:text-blue-800"
          >
            <Download className="w-4 h-4" />
            Download
          </a>
        </div>
      </div>

      {/* Player Controls */}
      <div className="p-4 border-b border-gray-100">
        <div className="flex items-center gap-4 mb-4">
          <button 
            onClick={togglePlay}
            className="text-blue-600 hover:text-blue-800"
          >
            {isPlaying ? (
              <PauseCircle className="w-10 h-10" />
            ) : (
              <PlayCircle className="w-10 h-10" />
            )}
          </button>
          <div className="flex-1">
            <div className="h-2 bg-gray-200 rounded-full">
              <div 
                className="h-2 bg-blue-500 rounded-full" 
                style={{ width: `${(currentTime / 180) * 100}%` }}
              />
            </div>
            <div className="flex justify-between mt-1">
              <span className="text-xs text-gray-500">{formatTime(currentTime)}</span>
              <span className="text-xs text-gray-500">{recording.duration}</span>
            </div>
          </div>
        </div>
      </div>

      {/* Tabs */}
      <div className="flex border-b border-gray-100">
        <button
          className={`flex-1 px-4 py-2 text-sm font-medium ${
            showTranscript
              ? "text-blue-600 border-b-2 border-blue-600"
              : "text-gray-500 hover:text-gray-700"
          }`}
          onClick={() => setShowTranscript(true)}
        >
          <div className="flex items-center justify-center gap-2">
            <MessageSquare className="w-4 h-4" />
            Transcript
          </div>
        </button>
        <button
          className={`flex-1 px-4 py-2 text-sm font-medium ${
            !showTranscript
              ? "text-blue-600 border-b-2 border-blue-600"
              : "text-gray-500 hover:text-gray-700"
          }`}
          onClick={() => setShowTranscript(false)}
        >
          <div className="flex items-center justify-center gap-2">
            <Star className="w-4 h-4" />
            Key Moments
          </div>
        </button>
      </div>

      {/* Content */}
      <div className="flex-1 overflow-y-auto p-4">
        {showTranscript ? (
          <div className="space-y-4">
            <p className="text-sm text-gray-600 leading-relaxed">
              {recording.transcription}
            </p>
          </div>
        ) : (
          <div className="space-y-3">
            {recording.keyMoments.map((moment, index) => (
              <div 
                key={index}
                className="flex items-start gap-3 p-2 rounded-lg hover:bg-gray-50"
              >
                <div className="flex items-center justify-center w-8 h-8 rounded-full bg-blue-50">
                  <Clock className="w-4 h-4 text-blue-600" />
                </div>
                <div>
                  <div className="flex items-center gap-2">
                    <span className="text-sm font-medium text-gray-900">
                      {moment.time}
                    </span>
                  </div>
                  <p className="text-sm text-gray-600 mt-1">
                    {moment.text}
                  </p>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}

