import React from 'react';
import { Smile, Meh, Frown } from 'lucide-react';

const defaultData = {
  positive: 65,
  neutral: 25,
  negative: 10,
  keywords: ["pricing", "features", "quality", "support"],
  details: [
    { date: "Jul 30", positive: 70, neutral: 20, negative: 10 },
    { date: "Jul 29", positive: 65, neutral: 25, negative: 10 },
    { date: "Jul 28", positive: 60, neutral: 30, negative: 10 }
  ],
  overall: 0.75,
  segments: [
    { score: 0.8, text: "Customer expressed interest in gold rate", timestamp: "0:30" },
    { score: 0.6, text: "Discussed current market trends", timestamp: "1:45" },
    { score: 0.9, text: "Positive response to special offer", timestamp: "2:15" }
  ]
};

const SentimentAnalysis = ({ sentiment = defaultData }) => {
  const getSentimentIcon = (score) => {
    if (score >= 0.6) return <Smile className="w-6 h-6 text-green-500" />;
    if (score >= 0.4) return <Meh className="w-6 h-6 text-yellow-500" />;
    return <Frown className="w-6 h-6 text-red-500" />;
  };

  const getSentimentLabel = (score) => {
    if (score >= 0.6) return 'Positive';
    if (score >= 0.4) return 'Neutral';
    return 'Negative';
  };

  if (!sentiment) return null;

  return (
    <div className="bg-white rounded-lg shadow p-6">
      <h3 className="text-lg font-medium mb-4">Sentiment Analysis</h3>
      <div className="grid grid-cols-1 gap-4">
        <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
          <div className="flex items-center gap-3">
            {getSentimentIcon(sentiment.overall)}
            <div>
              <p className="font-medium">Overall Sentiment</p>
              <p className="text-sm text-gray-600">{getSentimentLabel(sentiment.overall)}</p>
            </div>
          </div>
          <span className="text-lg font-semibold">{Math.round((sentiment.overall || 0) * 100)}%</span>
        </div>
        
        <div className="space-y-3">
          {(sentiment.segments || []).map((segment, index) => (
            <div key={index} className="flex items-center gap-3 p-2 border-b">
              <div className="w-8">{getSentimentIcon(segment.score)}</div>
              <div className="flex-1">
                <p className="text-sm text-gray-600">{segment.text}</p>
                <p className="text-xs text-gray-500">{segment.timestamp}</p>
              </div>
              <span className="text-sm font-medium">{Math.round(segment.score * 100)}%</span>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default SentimentAnalysis;
