import React from 'react';
import OutreachManager from '../components/campaign/OutreachManager';

const VoiceCampaign = ({
  contacts,
  setContacts,
  onStartCampaign,
  onBackToDashboard
}) => {

  return (
    <OutreachManager
      contacts={contacts}
      setContacts={setContacts}
      onStartOutreach={onStartCampaign}
      onBackToDashboard={onBackToDashboard}
    />
  );
};

export default VoiceCampaign;
