import React from "react";
import { Phone, Check, X, Clock } from "lucide-react";

const mockCalls = [
  {
    id: 1,
    name: "Ravi Kumar",
    phone: "+91 9876543210",
    language: "tamil",
    status: "completed",
    duration: "2m 45s",
    timestamp: "2 mins ago",
    sentiment: "positive"
  },
  {
    id: 2,
    name: "Priya Singh",
    phone: "+91 9876543211",
    language: "english",
    status: "completed",
    duration: "3m 15s",
    timestamp: "15 mins ago",
    sentiment: "neutral"
  },
  {
    id: 3,
    name: "Amit Patel",
    phone: "+91 9876543212",
    language: "hindi",
    status: "completed",
    duration: "1m 30s",
    timestamp: "25 mins ago",
    sentiment: "positive"
  },
  {
    id: 4,
    name: "Sara Khan",
    phone: "+91 9876543213",
    language: "english",
    status: "completed",
    duration: "4m 15s",
    timestamp: "1 hour ago",
    sentiment: "positive"
  },
  {
    id: 5,
    name: "Vijay Kumar",
    phone: "+91 9876543214",
    language: "tamil",
    status: "completed",
    duration: "2m 30s",
    timestamp: "2 hours ago",
    sentiment: "neutral"
  }
];

function CallsTable({ calls = mockCalls, loading, onSelectPerson }) {
  const getStatusIcon = (status) => {
    switch (status) {
      case 'completed':
        return <Check className="h-4 w-4 text-green-500" />;
      case 'failed':
        return <X className="h-4 w-4 text-red-500" />;
      case 'in-progress':
        return <Clock className="h-4 w-4 text-blue-500" />;
      default:
        return null;
    }
  };

  return (
    <div className="overflow-x-auto">
      <table className="min-w-full">
        <thead>
          <tr>
            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Contact
            </th>
            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Phone Number
            </th>
            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Language
            </th>
            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Status
            </th>
            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Duration
            </th>
            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Sentiment
            </th>
            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Time
            </th>
          </tr>
        </thead>
        <tbody className="bg-white">
          {loading ? (
            <tr>
              <td colSpan="6" className="px-6 py-4 text-center text-gray-500">
                Loading...
              </td>
            </tr>
          ) : (
            calls.map((call) => (
              <tr 
                key={call.id} 
                className="hover:bg-gray-50 cursor-pointer"
                onClick={() => onSelectPerson(call.id)}
              >
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="flex items-center">
                    <div className="h-8 w-8 flex-shrink-0 bg-gray-100 rounded-full flex items-center justify-center">
                      <Phone className="h-4 w-4 text-gray-500" />
                    </div>
                    <div className="ml-4">
                      <div className="text-sm font-medium text-gray-900">{call.name}</div>
                    </div>
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {call.phone}
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className="inline-flex text-xs leading-5 font-medium capitalize">
                    {call.language}
                  </span>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="flex items-center">
                    {getStatusIcon(call.status)}
                    <span className="ml-2 text-sm text-gray-500 capitalize">{call.status}</span>
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {call.duration}
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                    call.sentiment === 'positive' 
                      ? 'bg-green-100 text-green-800'
                      : call.sentiment === 'negative'
                      ? 'bg-red-100 text-red-800'
                      : 'bg-gray-100 text-gray-800'
                  }`}>
                    {call.sentiment}
                  </span>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {call.timestamp}
                </td>
              </tr>
            ))
          )}
        </tbody>
      </table>
    </div>
  );
}

export default CallsTable;