import React from "react";
import { ArrowLeft } from 'lucide-react';
import TrendChart from "../components/analytics/TrendChart";
import CallStatistics from "../components/analytics/CallStatistics";
import SentimentAnalysis from "../components/analytics/SentimentAnalysis";
import CallSummary from "../components/analytics/CallSummary";
import RecordingPlayer from "../components/analytics/RecordingPlayer";

const CallPersonDashboard = ({ personId, onBack }) => {

  // Mock data for individual person
  const mockPersonData = {
    id: personId,
    name: "Ravi Kumar",
    phone: "+91 9876543210",
    statistics: {
      totalCalls: 5,
      avgDuration: "2m 30s",
      completionRate: "80%",
      callbackRequests: 2
    },
    trends: {
      dates: ["Jul 25", "Jul 26", "Jul 27", "Jul 28", "Jul 29", "Jul 30"],
      calls: [2, 1, 0, 1, 0, 1],
      durations: [150, 120, 0, 180, 0, 140]
    },
    sentiments: {
      positive: 60,
      neutral: 30,
      negative: 10,
      keywords: ["interested", "price", "features", "demo"]
    },
    latestRecording: {
      url: "path/to/recording.mp3",
      duration: "2:30",
      timestamp: "2023-07-30T10:00:00Z"
    },
    callSummary: {
      summary: "Shows high interest in product features and pricing. Requested demo.",
      topics: ["Product Features", "Pricing", "Demo Request"],
      nextSteps: ["Schedule Demo", "Send Price List"]
    }
  };

  return (
    <div className="space-y-8">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <button
            onClick={onBack}
            className="flex items-center text-gray-600 hover:text-gray-900 mb-4"
          >
            <ArrowLeft className="h-4 w-4 mr-2" />
            Back to Dashboard
          </button>
          <h1 className="text-2xl font-semibold">
            {mockPersonData.name}'s Call Analytics
          </h1>
          <p className="text-gray-600">{mockPersonData.phone}</p>
        </div>
      </div>

      {/* Statistics Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <CallStatistics 
          title="Total Calls"
          value={mockPersonData.statistics.totalCalls}
          trend="+2 from last week"
        />
        <CallStatistics 
          title="Average Duration"
          value={mockPersonData.statistics.avgDuration}
          trend="+30s from last week"
        />
        <CallStatistics 
          title="Completion Rate"
          value={mockPersonData.statistics.completionRate}
          trend="+5% from last week"
        />
        <CallStatistics 
          title="Callback Requests"
          value={mockPersonData.statistics.callbackRequests}
          trend="+1 from last week"
        />
      </div>

      {/* Charts Row */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <TrendChart data={mockPersonData.trends} />
        <SentimentAnalysis data={mockPersonData.sentiments} />
      </div>

      {/* Bottom Row */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <RecordingPlayer recording={mockPersonData.latestRecording} />
        <CallSummary data={mockPersonData.callSummary} />
      </div>
    </div>
  );
};

export default CallPersonDashboard;
